import * as React from 'react';

import { Button } from 'antd';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { getWeather } from 'src/store/action-creators/getWeatherInfo';
import { registerNotification, toSubscribe as toSubscribeActionCreator } from 'src/store/action-creators/registerNotification';
import { IApplicationState } from 'src/store/applicationState';

interface IPropsFromState {
  isSubscribed: boolean,
  pushManager: any,
  subscriptionEndpoint?: string,
  sunrise?: string,
  sunset?: string,
}

interface IPropsFromDispatch{
  getWeather: () => void,
  registerNotification: () => void,
  subscribe: (pushManager: any, toSubscribe: boolean) => void,
}

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  isSubscribed: state.isSubscribed,
  pushManager: state.pushManager,
  subscriptionEndpoint: state.subscriptionEndpoint,
  sunrise: state.sunrise,
  sunset: state.sunset,
})

const mapDispatchToProps = (dispatch: Dispatch) : IPropsFromDispatch => ({
  getWeather: () => dispatch(getWeather()),
  registerNotification: () => dispatch(registerNotification()),
  subscribe: (pushManager, toSubscribe) => dispatch(toSubscribeActionCreator(pushManager, toSubscribe)),
});

class Home extends React.PureComponent<IPropsFromState & IPropsFromDispatch> {
  public componentDidMount() {
    this.props.getWeather();
    // To comment for using caching sw
    this.props.registerNotification();
  }

  public render() {
    const buttonText = (this.props.isSubscribed) ? 'Unsubscribe' : 'Subscribe';

    return (
      <React.Fragment>
        <div>The time now is {(new Date()).toString()}</div>
        <div>Today's sunrise is {this.props.sunrise}</div>
        <div>Today's sunset is {this.props.sunset}</div>
        <Button onClick={this.handleSubscribe}>{buttonText}</Button>
        {
          this.props.subscriptionEndpoint ?
          <div style={{ background: '#D3D3D3'}}>{this.props.subscriptionEndpoint}</div> :
          ''
        }
      </React.Fragment>
    )
  }

  private handleSubscribe = () => {
    this.props.subscribe(this.props.pushManager, !(this.props.isSubscribed));
  }
}

export default connect<IPropsFromState,IPropsFromDispatch>(mapStateToProps,mapDispatchToProps)(Home);



