import { Action, ActionCreator } from 'redux';
import { ActionTypes } from '../actionTypes';

export interface ISetPushManagerAction extends Action {
  payload: {
    pushManager: any
  }
}

export interface ISetSubscribedAction extends Action {
  payload: {
    isSubscribed: boolean,
  }
}

export interface ISubscribeAction extends Action {
  payload: {
    pushManager: any,
    subscribe: boolean,
  }
}

export interface ISetSubscriptionEndpointAction extends Action {
  payload: {
    subscriptionEndpoint: string,
  }
}

export const registerNotification: ActionCreator<Action> = () => ({
  type: ActionTypes.REGISTER_NOTIFICATION,
});

export const setPushManager: ActionCreator<ISetPushManagerAction> = (pushManager: any) => ({
  payload: {
    pushManager,
  },
  type: ActionTypes.SET_PUSH_MANAGER,
});

export const setSubscribed: ActionCreator<ISetSubscribedAction> = (isSubscribed: boolean) => ({
  payload: {
    isSubscribed,
  },
  type: ActionTypes.SET_SUBSCRIBED,
});

export const toSubscribe: ActionCreator<ISubscribeAction> = (pushManager: any, subscribe: boolean) => ({
  payload: {
    pushManager,
    subscribe
  },
  type: ActionTypes.SUBSCRIBE
});

export const setSubscriptionEndpoint: ActionCreator<ISetSubscriptionEndpointAction> = (subscriptionEndpoint: string) => ({
  payload: {
    subscriptionEndpoint,
  },
  type: ActionTypes.SET_SUBSCRIPTION_ENDPOINT,
})



