import { put, takeLatest } from 'redux-saga/effects';

import registerServiceWorkerNotification from '../../registerServiceWorkerNotification';
import { ISubscribeAction, setPushManager, setSubscribed, setSubscriptionEndpoint, } from '../action-creators/registerNotification';
import { ActionTypes } from '../actionTypes';
import * as Utils from '../utils';

export function* registerNotificationWatcher() {
  yield takeLatest(ActionTypes.REGISTER_NOTIFICATION, registerNotification);
}

export function* registerNotification() {
  try {
    const swRegistration = yield registerServiceWorkerNotification();
    const pushManager = swRegistration.pushManager;
    yield put(setPushManager(swRegistration.pushManager));
    const subscriptions = yield pushManager.getSubscription();
    yield put(setSubscribed(!(subscriptions === null)));
  } catch (err) {
    console.log(err);
  }
}

export function* subscribeWatcher() {
  yield takeLatest(ActionTypes.SUBSCRIBE, subscribe);
}

export function* subscribe(action: ISubscribeAction) {
  const applicationServerKey = Utils.urlB64ToUint8Array('BFOUfzOVSGALe3AWUG9B5KAiqxtVyd69z0QOCT5ajx23HJJon0MUstrdmgF5Wf5xPNwUr_fh5z1vdpIefbAWjR8');

  try {
    const subscription = yield action.payload.pushManager.getSubscription();
    if (subscription) {
      yield subscription.unsubscribe();
      yield put(setSubscribed(false));
      yield put(setSubscriptionEndpoint(undefined));
    } else {
      const subscriptionResponse = yield action.payload.pushManager.subscribe({
        applicationServerKey,
        userVisibleOnly: true,
      })
      yield put(setSubscribed(true));
      yield put(setSubscriptionEndpoint(JSON.stringify(subscriptionResponse)));
    }

  } catch (err) {
    console.log(err);
  }
}



