import { IApplicationState, IUser } from '../applicationState';

export const addOrReplaceUser = (state: IApplicationState, user: IUser): void => {
  state.users[user.id] = user;
}

export const setCurrentUser = (state: IApplicationState, user?: IUser): void => {
  if (user) {
    state.currentUser = user.id;
    state.isAuthenticated = true;
  } else {
    state.currentUser = undefined;
    state.isAuthenticated = false;
  }
}

export const setLoading = (state: IApplicationState, loading: boolean): void => {
  state.isLoading = loading;
}

export const setSelectedHeaderKey = (state: IApplicationState, selectedKey?: string): void => {
  state.selectedHeaderKey = selectedKey;
}

export const setLocation = (state: IApplicationState, location: [number,number]): void => {
  state.currentLocation = location;
}

export const setOnline = (state: IApplicationState, isOnline: boolean): void => {
  state.isOnline = isOnline;
}

export const setWeather = (state: IApplicationState, sunrise: string, sunset: string): void => {
  state.sunrise = sunrise;
  state.sunset = sunset;
}

export const setPushManger = (state: IApplicationState, pushManager: any): void => {
  state.pushManager = pushManager;
}

export const setSubscribed = (state: IApplicationState, isSubscribed: boolean): void => {
  state.isSubscribed = isSubscribed;
}

export const setSubscriptionEndpoint = (state: IApplicationState, subscriptionEndpoint: string): void => {
  state.subscriptionEndpoint = subscriptionEndpoint;
}

export const urlB64ToUint8Array = (base64String: string): any => {
  const padding = '='.repeat((4 - base64String.length % 4) % 4);
  const base64 = (base64String + padding)
    .replace(/\-/g, '+')
    .replace(/_/g, '/');

  const rawData = window.atob(base64);
  const outputArray = new Uint8Array(rawData.length);

  for (let i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i);
  }
  return outputArray;
}



